import React, { useState } from 'react';


// HOOK DE REGISTRO
const Register = () => {

    // DATOS DE ESTADO
    const [datos, setDatos] = useState({
        UserName: '',
        Correo: '',
        Clave: '',
        ConfirmarClave: ''
    })

    // evento de registrar
    const handleRegister = e => {
        e.preventDefault();

        console.log("Enviando");
        console.log(datos);

        let paquete = {
            method: "POST",
            mode: 'cors',
            body: {
                UserName: datos['UserName'],
                Correo: datos['Correo'],
                Clave: datos['Clave']
            }
        };

        console.log(paquete);

        try {
            fetch('Register', paquete).then(response => {
                return response.json();
            }).then(response => {

                console.log(response);

            });
        } catch (error) {
            console.log("Error al realizar la peticion" + error.mess());
        }
    }

    // al cambiar algún dato del formulario
    const handleChangeData = e => {

        setDatos({
            ...datos,
            [e.target.name]: e.target.value
        })

        console.log(datos);

    }

    return (
        <div>
            <div>

            </div>

            <div>
                <div>
                    <form onSubmit={handleRegister}>
                        <input name="UserName" type="text"
                            placeholder="Nombre de usuario"
                            onChange={handleChangeData} />
                        <input name="Correo" type="email"
                            placeholder="Correo Electronico"
                            onChange={handleChangeData} />
                        <input name="Clave" type="password"
                            placeholder="Contraseña"
                            onChange={handleChangeData} />
                        <input name="ConfirmarClave" type="password"
                            placeholder="Confirmar Contraseña"
                            onChange={handleChangeData} />
                        <button type="submit">Relizar registro</button>
                    </form>
                </div>

            </div>
        </div>
    );

}

export default Register;